.sidebar{
    height: 100vh;
    flex:0.2;
    background-color: #040404;
    padding-left: 10px;
    padding-right: 10px;
    color: white;
    min-width: 230px;

}

.sidebar > hr{
    border: 1px solid #282828;
    width: 90%;
    margin: 10px;
}

.sidebar > p{
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}


.sidebar_logo{
    height: 70px;
    padding: 10px;
    margin-right: auto;
}

.sidebar_title{
    margin-left:10px;
    padding: 5px;
    font-size: 12px;
}